<template>
  <div >
    <!-- 筛选区 -->
    <section class="cms-list-query" style="padding-top:0">
      <div class="query-group">
        <label>菜单组名称</label>
        <el-input v-model="queryParams.menuGroupName" @keyup.native.enter="query"></el-input>
      </div>
      <div class="query-group">
        <cms-button size="small" :colorPicker='true' @click="query">查询</cms-button>
      </div>
    </section>
    <!-- 操作区 -->
    <cms-sticky>
    <section class="cms-batch">
      <cms-button icon="add" type="success" size="small" @click="$routerLink('/wechatMenuMange/individuationMenu/save','save')">新增</cms-button>
    </section>
    </cms-sticky>
    <el-table  :data="tableData" style="width: 100%" stripe v-loading="loading" @selection-change='getCheckItems' @row-click='checkRows' ref='dataTable'>
      <el-table-column prop="menuGroupName" label="菜单组名称" min-width='120'></el-table-column>
      <el-table-column prop="roleNames" label="显示对象（多个为并且关系）" min-width="450" align="center">
        <template slot-scope="scope">
          <span v-if="scope.row.sexDictCode">【{{sex[parseInt(scope.row.sexDictCode)]}}】</span>
          <span v-if="scope.row.wTag">【{{scope.row.wTag.tagName}}】</span>
          <span v-if="scope.row.clientDictCode">【{{scope.row.clientDictCode | statusFilter}}】</span>
          <span v-if="scope.row.countryName">【{{scope.row.countryName}}】</span>
          <span v-if="scope.row.languageDictCode">【{{scope.row.languageDictCode|languageFilter}}】</span>
          <span v-if="scope.row.area">【{{scope.row.area.areaName}}】</span>
        </template>
      </el-table-column>
      <el-table-column prop="status" label="是否生效（微信支持同时生效多组个性化菜单）" min-width="350" align="center">
        <template slot-scope="scope">
          <span v-if="scope.row.status === 1" style="color:green">生效中</span>
          <span v-if="scope.row.status === 2" style="color:#4184B7;cursor:pointer" @click="efficient(scope.row.id)">推送至微信</span>
        </template>
      </el-table-column>
      <el-table-column label="操作" width='350' fixed="right" align="right"  header-align="center">
        <div slot-scope="scope">
           <cms-button icon='edit' size='small' type='info' @click.stop="$routerLink('/wechatMenuMange/individuationMenu/view','view',{id:scope.row.id})">查看</cms-button>
          <cms-button v-if="scope.row.status!==1" icon='edit' size='small' type='info' @click.stop="$routerLink('/wechatMenuMange/individuationMenu/edit','update',{id:scope.row.id})">编辑</cms-button>
          <cms-button  icon='edit' size='small' type='info' @click.stop="$routerLink('/wechatMenuMange/individuationMenu/edit','save',{id:scope.row.id})">复制并新增</cms-button>
          <cms-button  icon='error' size='small' type='info' @click.stop="deleteData(scope.row.id)">删除</cms-button>
        </div>
      </el-table-column>
    </el-table>
    <cms-pagination @change="getPage" :total='total'></cms-pagination>
  </div>
</template>
<script>
import list from '@/mixins/list'
import {
  menuManageList,
  menuManageDelete,
  menuManageUpdateStatus
} from '@/api/wechat/index.js'
var defaultData = {
  menuGroupName: '',
  menuGroupType: 2,
  size: 10,
  page: 1
}
export default {
  name: 'defaultMenuList',
  mixins: [list],

  data() {
    return {
      queryParams: Object.assign({}, defaultData),
      sex: ['', '男', '女', '不限性别']
    }
  },
  methods: {
    deleteData(id) {
      // 包装一层删除的操作
      this.deleteTableData(menuManageDelete, id) // 这个才是真实的删除方法
    },
    deleteDatas() {
      this.deleteTableDatas(menuManageDelete, this.ids) // 这个才是真实的删除方法
    },
    efficient(id) {
      this.$confirm(
        '是否要将此公众号菜单组生效并置顶',
        '系统提示',
        {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }
      )
        .then(() => {
          menuManageUpdateStatus({ id: id, status: 1 }).then(res => {
            if (res.code === 200) {
              this.$successMessage('生效成功')
              this.getTableData(this.callback, this.queryParams, this.isPage)
            } else {
              this.$errorMessage('生效失败,' + res.message)
            }
          })
        })
        .catch(() => {})
    }
  },
  activated() {
    // 缓存触发
    if (this.$route.query.type === 'list') {
      this.queryParams = Object.assign({}, defaultData)
      // 这个值一定要用动态的变量
      this.getTableData(menuManageList, this.queryParams)
    }
  },
  filters: {
    statusFilter(val) {
      var arr = ['', 'IOS', 'Android', '不限客户端']
      return arr[parseInt(val)]
    },
    languageFilter(val) {
      var arr = ['', '简体中文', '繁体中文TW', '繁体中文HK', '英文', '印尼', '马来', '西班牙', '韩国', '意大利', '日本', '波兰', '葡萄牙', '俄国', '泰文', '越南', '阿拉伯语', '北印度', '希伯来', '土耳其', '德语', '法语']
      return arr[parseInt(val)]
    }
  },
  created() {
    // 首次触发
    this.getTableData(menuManageList, this.queryParams)
  }
}
</script>

<style scoped>
.btn-a{
  background-color: rgb(24, 138, 226);
  width: 50px;
  height: 28px;
  color: #ffffff;
  border-radius: 5px;
  text-align: center;
  line-height: 28px;
  font-size: 14px;
  display: block;
}

.el-tabs__header {
    padding: 0;
    position: relative;
    margin: 0 0 10px !important;
}

</style>



