<template>
  <div class="cms-list-layout" style="padding-top:6px">
    <el-tabs v-model="activeName" ref='productTab'>
      <el-tab-pane label="默认菜单" name="first">
        <default-menu></default-menu>
      </el-tab-pane>
     <el-tab-pane label="个性化菜单" name="second" lazy>
        <individuation-menu></individuation-menu>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
import defaultMenu from './defaultMenu/list.vue'
import individuationMenu from './individuationMenu/list.vue'
export default {

  components: {
    defaultMenu,
    individuationMenu
  },
  data() {
    return {
      status: true,
      activeName: 'first'
    }
  },
  methods: {}

}
</script>

<style>
</style>



