<template>
<div class="menu-all">
  <div >
    <!-- 筛选区 -->
    <section class="cms-list-query" style="padding-top:0">
      <div class="query-group">
        <label>菜单组名称</label>
        <el-input v-model="queryParams.menuGroupName" @keyup.native.enter="query"></el-input>
      </div>
      <div class="query-group">
        <cms-button size="small" :colorPicker='true' @click="query">查询</cms-button>
      </div>
    </section>
    <!-- 操作区 -->
    <section class="cms-batch">
      <cms-button icon="add" type="success" size="small" @click="$routerLink('/wechatMenuMange/defaultMenu/save','save')">新增</cms-button>
    </section>
    <el-table :data="tableData" style="width: 100%" stripe v-loading="loading" @selection-change='getCheckItems' @row-click='checkRows' ref='dataTable'>
      <el-table-column prop="menuGroupName" label="菜单组名称" min-width='120'></el-table-column>
      <el-table-column  label="显示对象" min-width="160" align="center">
        <template>
          <span>全部粉丝</span>
        </template>
      </el-table-column>
      <el-table-column prop="status" label="是否生效（微信只允许同时生效一组默认菜单）" min-width="350" align="center">
        <template slot-scope="scope">
          <span v-if="scope.row.status === 1" style="color:green">生效</span>
          <span v-if="scope.row.status === 2" style="color:#4184B7;cursor:pointer" @click="efficient(scope.row.id)">生效并置顶</span>
        </template>
      </el-table-column>
      <el-table-column label="操作" width='175' fixed="right" align="right"  header-align="center">
        <div slot-scope="scope">
          <cms-button  icon='edit' size='small' type='info' @click.stop="$routerLink('/wechatMenuMange/defaultMenu/edit','update',{id:scope.row.id,status: scope.row.status,})">编辑</cms-button>
          <cms-button v-if="scope.row.status === 2"  icon='error' size='small' type='info' @click.stop="deleteData(scope.row.id)">删除</cms-button>
        </div>
      </el-table-column>
    </el-table>
    <cms-pagination @change="getPage" :total='total'></cms-pagination>
  </div></div>
</template>
<script>
import list from '@/mixins/list'
import {
  menuManageList,
  menuManageDelete,
  menuManageUpdateStatus
} from '@/api/wechat/index.js'
var defaultData = {
  menuGroupName: '',
  menuGroupType: 1,
  size: 10,
  page: 1
}
export default {
  name: 'defaultMenuList',
  mixins: [list],
  data() {
    return {
      queryParams: Object.assign({}, defaultData)
    }
  },
  methods: {
    deleteData(id) {
      // 包装一层删除的操作
      this.deleteTableData(menuManageDelete, id) // 这个才是真实的删除方法
    },
    deleteDatas() {
      this.deleteTableDatas(menuManageDelete, this.ids) // 这个才是真实的删除方法
    },
    efficient(id) {
      this.$confirm(
        '是否要将此公众号菜单组生效并置顶',
        '系统提示',
        {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }
      )
        .then(() => {
          menuManageUpdateStatus({ id: id, status: 1 }).then(res => {
            if (res.code === 200) {
              this.$successMessage('生效成功')
              this.getTableData(this.callback, this.queryParams, this.isPage)
            } else {
              this.$errorMessage('生效失败,' + res.message)
            }
          })
        })
        .catch(() => {})
    }
  },
  activated() {
    // 缓存触发
    if (this.$route.query.type === 'list') {
      this.queryParams = Object.assign({}, defaultData)
      // 这个值一定要用动态的变量
      this.getTableData(menuManageList, this.queryParams)
    }
  },
  created() {
    // 首次触发
    this.getTableData(menuManageList, this.queryParams)
  }
}
</script>

<style scoped>
.btn-a{
  background-color: rgb(24, 138, 226);
  width: 50px;
  height: 28px;
  color: #ffffff;
  border-radius: 5px;
  text-align: center;
  line-height: 28px;
  font-size: 14px;
  display: block;
}


</style>



